import React, {useEffect, useState} from 'react';
import {doc, onSnapshot} from "firebase/firestore";
import {
    db,
    joinBlueTeam,
    joinRedTeam,
    leaveLobby,
    saveWords,
    setBlueTeamCaptain,
    setRedTeamCaptain,
    startGameWithWords,
} from './firebase-config';
import Game from './Game';
import {useLocation, useNavigate, useParams} from "react-router-dom";
import {Box, Button, Grid, Paper, TextField, Typography,} from '@mui/material';

const Lobby = () => {
    const navigate = useNavigate();
    const {lobbyId} = useParams();
    const location = useLocation();
    const nickname = location.state?.nickname;

    const [allNicknames, setAllNicknames] = useState([]);
    const [blueTeam, setBlueTeam] = useState([]);
    const [redTeam, setRedTeam] = useState([]);
    const [captains, setCaptains] = useState({});
    const [isGameStarted, setIsGameStarted] = useState(false);
    const [wordList, setWordList] = useState("");
    const [leader, setLeader] = useState();
    const [lobbyCode, setLobbyCode] = useState();


    useEffect(() => {
        if (lobbyId) {
            const unsubscribe = onSnapshot(doc(db, "lobbies", lobbyId), (doc) => {
                if (doc.exists()) {
                    const data = doc.data();
                    setAllNicknames(data.nicknames || []);
                    setBlueTeam(data.blueTeam || []);
                    setRedTeam(data.redTeam || []);
                    setCaptains(data.captains || {});
                    setIsGameStarted(data.gameState === "started");
                    setWordList(data.words ? data.words.join(', ') : '');
                    setLeader(data.leader);
                    setLobbyCode(data.code || '');
                } else {
                    navigate('/');
                }
            });

            return () => unsubscribe();
        }
    }, [lobbyId, navigate]);

    const handleJoinTeam = async (team) => {
        if (team === "blue") {
            await joinBlueTeam(lobbyId, nickname, redTeam, captains);
        } else if (team === "red") {
            await joinRedTeam(lobbyId, nickname, blueTeam, captains);
        }
    };

    const handleSetCaptain = async (team) => {
        if (team === "blue") {
            await setBlueTeamCaptain(lobbyId, nickname, blueTeam);
        } else if (team === "red") {
            await setRedTeamCaptain(lobbyId, nickname, redTeam);
        }
    };

    const startGame = async () => {
        const words = wordList.split(',').map(word => word.trim());
        if (words.length < 25) {
            alert("   25 .");
            return;
        }
        try {
            await startGameWithWords(lobbyId, words);
            setIsGameStarted(true);
        } catch (error) {
            console.error("Could not start the game:", error);
        }
    };

    const handleWordListChange = async (e) => {
        const newWordList = e.target.value;
        setWordList(newWordList);
        if (newWordList) {
            await saveWords(lobbyId, newWordList.split(',').map(word => word.trim()));
        }
    };

    const handleLeaveLobby = async () => {
        try {
            navigate('/');
            await leaveLobby(lobbyId, nickname);
        } catch (error) {
            console.error("Could not leave the lobby:", error);
        }
    };

    const handleKickPlayer = async (playerToKick) => {
        try {
            await leaveLobby(lobbyId, playerToKick);
            console.log(`${playerToKick} has been kicked from the lobby.`);
            if (playerToKick === nickname) {
                navigate('/');
            }
        } catch (error) {
            console.error("Could not kick player:", error);
        }
    };

    const handleGameEnd = () => {
        setIsGameStarted(false);
    };

    if (isGameStarted) {
        const playerRole = captains.blue === nickname ? 'captain' : (captains.red === nickname ? 'captain' : 'teammate');
        const playerTeam = blueTeam.includes(nickname) ? 'blue' : (redTeam.includes(nickname) ? 'red' : undefined);

        return (
            <Game
                lobbyId={lobbyId}
                playerRole={playerRole}
                playerTeam={playerTeam}
                blueTeam={blueTeam}
                redTeam={redTeam}
                onGameEnd={handleGameEnd}
                captains={captains}
            />
        );
    }

    const canStartGame = allNicknames.length === 4;

    return (
        <>
            <Typography variant="h4" gutterBottom>
                Lobby Code: {lobbyCode}
            </Typography>

            <Paper elevation={3} sx={{p: 2, mb: 4}}>
                <Typography variant="h6" gutterBottom>
                    Nicknames:
                </Typography>
                <Box sx={{mb: 2}}>
                    {allNicknames.map((player) => (
                        <Box key={player} sx={{display: 'flex', alignItems: 'center', mb: 1}}>
                            <Typography>{player}</Typography>
                            {nickname === leader && player !== leader && (
                                <Button
                                    variant="outlined"
                                    color="error"
                                    onClick={() => handleKickPlayer(player)}
                                    sx={{ml: 2}}
                                >
                                    Kick
                                </Button>
                            )}
                        </Box>
                    ))}
                </Box>
            </Paper>
            <Grid container spacing={2}>
                <Grid item xs={12} md={4}>
                    <Paper elevation={3} sx={{p: 2}}>
                        <Typography variant="h6" gutterBottom>
                            Blue Team {captains.blue ? `(Captain: ${captains.blue})` : ""}
                        </Typography>
                        {blueTeam.map(player => (
                            <Typography key={player}>{player}</Typography>
                        ))}
                        <Button
                            variant="contained"
                            color="primary"
                            onClick={() => handleJoinTeam("blue")}
                            sx={{mt: 2}}
                        >
                            Join Blue
                        </Button>
                        <Button
                            variant="outlined"
                            color="primary"
                            onClick={() => handleSetCaptain("blue")}
                            sx={{mt: 1}}
                        >
                            Become Blue Captain
                        </Button>
                    </Paper>
                </Grid>

                <Grid item xs={12} md={4}>
                    <Paper elevation={3} sx={{p: 2}}>
                        <TextField
                            label="Enter words here..."
                            multiline
                            rows={8}
                            value={wordList}
                            onChange={handleWordListChange}
                            fullWidth
                            variant="outlined"
                            sx={{mb: 2}}
                        />
                        <Box sx={{display: 'flex', justifyContent: 'space-between'}}>
                            <Button
                                variant="contained"
                                color="primary"
                                onClick={startGame}
                                disabled={!canStartGame || wordList.split(',').length < 25}
                                sx={{mr: 1}}
                            >
                                Start Game
                            </Button>
                            <Button
                                variant="outlined"
                                color="error"
                                onClick={handleLeaveLobby}
                            >
                                Leave Lobby
                            </Button>
                        </Box>
                    </Paper>
                </Grid>

                <Grid item xs={12} md={4}>
                    <Paper elevation={3} sx={{p: 2}}>
                        <Typography variant="h6" gutterBottom>
                            Red Team {captains.red ? `(Captain: ${captains.red})` : ""}
                        </Typography>
                        {redTeam.map(player => (
                            <Typography key={player}>{player}</Typography>
                        ))}
                        <Button
                            variant="contained"
                            color="secondary"
                            onClick={() => handleJoinTeam("red")}
                            sx={{mt: 2}}
                        >
                            Join Red
                        </Button>
                        <Button
                            variant="outlined"
                            color="secondary"
                            onClick={() => handleSetCaptain("red")}
                            sx={{mt: 1}}
                        >
                            Become Red Captain
                        </Button>
                    </Paper>
                </Grid>
            </Grid>
        </>
    );
};

export default Lobby;
