import React, { useEffect, useState } from 'react';
import { db } from './firebase-config';
import { doc, onSnapshot, updateDoc } from "firebase/firestore";
import { Box, Button, Container, Grid, Typography } from '@mui/material';
import './index.css';

const Game = ({ lobbyId, playerRole, playerTeam, blueTeam, redTeam, onGameEnd }) => {
    const lobbyRef = doc(db, "lobbies", lobbyId);
    const [grid, setGrid] = useState([]);
    const [currentTurn, setCurrentTurn] = useState('');
    const blueWordsLeft = grid.filter(cell => cell.color === 'blue' && !cell.revealed).length;
    const redWordsLeft = grid.filter(cell => cell.color === 'red' && !cell.revealed).length;
    const blackWordsLeft = grid.filter(cell => cell.color === 'black' && !cell.revealed).length;

    useEffect(() => {
        const unsubscribe = onSnapshot(doc(db, "lobbies", lobbyId), (doc) => {
            const data = doc.data();
            setGrid(data.grid || []);
            setCurrentTurn(data.turn || '');
        });

        return () => unsubscribe();
    }, [lobbyId, playerTeam]);

    const handleCellClick = async (cell, index, newGrid) => {
        if (currentTurn === playerTeam && !cell.revealed) {
            newGrid[index] = { ...cell, revealed: true };

            await updateDoc(lobbyRef, {
                grid: newGrid,
            }).catch((error) => {
                console.error("Error updating document: ", error);
            });

            if (cell.color !== playerTeam || cell.color === 'gray') {
                const nextTurn = playerTeam === 'blue' ? 'red' : 'blue';
                await updateDoc(lobbyRef, { turn: nextTurn }).catch((error) => {
                    console.error("Error updating turn: ", error);
                });
            }
        }
    };

    function getCellColor(cell, playerRole) {
        if (cell.revealed) return cell.color;
        if (playerRole === 'captain') {
            if (cell.color === 'gray') return 'gray';
            if (cell.color === 'black') return 'black';
            return cell.color;
        }
        return 'gray';
    }

    async function endTurn(playerTeam) {
        const nextTurn = playerTeam === 'blue' ? 'red' : 'blue';
        await updateDoc(lobbyRef, { turn: nextTurn });
    }

    if (blueWordsLeft === 0 || redWordsLeft === 0 || blackWordsLeft === 0) {
        const winner = blueWordsLeft === 0 ? 'Blue' : 'Red';
        const loser = winner === 'Blue' ? 'Red' : 'Blue';
        const winnerColor = winner.toLowerCase();

        return (
            <Container className="game-container" style={{
                display: 'flex',
                flexDirection: 'column',
                justifyContent: 'center',
                alignItems: 'center',
                height: '100vh'
            }}>
                {blackWordsLeft === 0 && (
                    <Typography variant="h5" style={{ color: winnerColor }}>
                        Player from {loser} team clicked on the black box!
                    </Typography>
                )}
                <Typography variant="h4" style={{ color: winnerColor }}>
                    {winner} Team Wins!
                </Typography>
                <Button variant="contained" color="primary" onClick={onGameEnd} style={{ marginTop: '20px' }}>
                    Return to Lobby
                </Button>
            </Container>
        );
    }

    return (
        <Container className="game-container">
            <Box className="header">
                <Box className="team-panel blue-team">
                    <Typography variant="h6">Blue Team</Typography>
                    <Typography>{blueTeam.join(', ')}</Typography>
                    <Typography>Words left: {blueWordsLeft}</Typography>
                </Box>
                <Box className="turn-info">
                    <Typography
                        variant="h5"
                        className={`turn-indicator ${currentTurn}`}
                        style={{ color: currentTurn === 'blue' ? '#1976d2' : '#9A1750' }}
                    >
                        Current Turn: {currentTurn.toUpperCase()} Team
                    </Typography>
                    <Button
                        variant="contained"
                        color="secondary"
                        onClick={() => endTurn(playerTeam)}
                        disabled={playerTeam !== currentTurn || playerRole === "captain"}
                    >
                        End Turn
                    </Button>
                </Box>
                <Box className="team-panel red-team">
                    <Typography variant="h6">Red Team</Typography>
                    <Typography>{redTeam.join(', ')}</Typography>
                    <Typography>Words left: {redWordsLeft}</Typography>
                </Box>
            </Box>
            <Grid className="grid">
                {grid.map((cell, index) => (
                    <Box
                        key={index}
                        className={`grid-cell ${getCellColor(cell, playerRole)} ${cell.revealed ? 'revealed' : ''}`}
                        onClick={() => {
                            if (playerRole !== 'captain') {
                                handleCellClick(cell, index, [...grid]);
                            }
                        }}>
                        {cell.word}
                    </Box>
                ))}
            </Grid>
        </Container>
    );
};

export default Game;
