export async function createLobby(lobbyCode, nickname) {
    const lobbiesRef = collection(db, "lobbies");
    const q = query(lobbiesRef, where("code", "==", lobbyCode));
    const querySnapshot = await getDocs(q);
    if (!querySnapshot.empty) {
        console.log("This lobby code is already in use");
        throw new Error("This lobby code is already in use");
    }

    try {
        const docRef = await addDoc(lobbiesRef, {
            code: lobbyCode,
            nicknames: [nickname],
            leader: nickname,
            captains: {},
            blueTeam: [],
            redTeam: [],
            gameState: "waiting",
        });
        return docRef.id;
    } catch (e) {
        console.error("Error adding document: ", e);
        throw e;
    }
}

export async function updateUseBots(lobbyId, useBots) {
    const lobbyRef = doc(db, "lobbies", lobbyId);
    await updateDoc(lobbyRef, {
        useBots: useBots
    });
}

export async function joinLobby(lobbyCode, nickname) {
    const lobbiesRef = collection(db, "lobbies");
    const q = query(lobbiesRef, where("code", "==", lobbyCode));
    const querySnapshot = await getDocs(q);

    if (querySnapshot.empty) {
        console.log("No matching lobby found");
        throw new Error("No matching lobby found");
    } else {
        const lobbyDoc = querySnapshot.docs[0];
        const lobbyData = lobbyDoc.data();

        if (lobbyData.nicknames.includes(nickname)) {
            console.log("Nickname is already in use");
            return;
        }

        if (lobbyData.nicknames.length >= 4) {
            console.log("Lobby is full");
            throw new Error("Lobby is full");
        }

        await updateDoc(lobbyDoc.ref, {
            nicknames: arrayUnion(nickname)
        });

        return lobbyDoc.id;
    }
}

export async function leaveLobby(lobbyId, nickname) {
    const lobbyRef = doc(db, "lobbies", lobbyId);
    const lobbyDoc = await getDoc(lobbyRef);
    const lobbyData = lobbyDoc.data();

    if (lobbyData.leader === nickname) {
        await deleteDoc(lobbyRef);
    } else {
        await updateDoc(lobbyRef, {
            nicknames: arrayRemove(nickname)
        });

        if (lobbyData.captains.blue === nickname) {
            await updateDoc(lobbyRef, {"captains.blue": ""});
        } else if (lobbyData.captains.red === nickname) {
            await updateDoc(lobbyRef, {"captains.red": ""});
        }

        if (lobbyData.blueTeam.includes(nickname)) {
            await updateDoc(lobbyRef, {blueTeam: arrayRemove(nickname)});
        } else if (lobbyData.redTeam.includes(nickname)) {
            await updateDoc(lobbyRef, {redTeam: arrayRemove(nickname)});
        }
    }
}

export async function joinBlueTeam(lobbyId, nickname, redTeam, captains) {
    const lobbyRef = doc(db, "lobbies", lobbyId);

    if (redTeam.includes(nickname)) {
        await updateDoc(lobbyRef, {
            redTeam: arrayRemove(nickname)
        });
    }

    if (captains.red === nickname) {
        await updateDoc(lobbyRef, {
            "captains.red": ""
        });
    }

    console.log("adding to blue team");
    await updateDoc(lobbyRef, {
        blueTeam: arrayUnion(nickname)
    });
}

