export async function saveWords(lobbyId, words) {
    const lobbyRef = doc(db, "lobbies", lobbyId);
    await updateDoc(lobbyRef, {
        words: words
    });
}

export async function startGameWithWords(lobbyId, words) {
    let shuffledWords = words.sort(() => 0.5 - Math.random()).slice(0, 25);
    let grid = shuffledWords.map((word, index) => {
        let color = "gray";
        if (index < 9) {
            color = "blue";
        } else if (index < 17) {
            color = "red";
        } else if (index === 17) {
            color = "black";
        }
        return {word, color, revealed: false};
    });

    grid = grid.sort(() => 0.5 - Math.random());

    const lobbyRef = doc(db, "lobbies", lobbyId);
    await updateDoc(lobbyRef, {
        grid,
        gameState: "started",
        turn: "blue",
    });
}
